<!-- 컴포넌트 UI 정의 -->
<template>
  <v-dialog v-model="flag" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn :input-value="on" v-bind="attrs" v-on="on" :color="btncolor ? btncolor : 'primary'" small class="mr-2">
        <span v-if="title" class="font-weight-bold white--text">{{ title }}</span> <span v-else>기본 배송지 등록</span>
      </v-btn>
    </template>
    <v-card>
      <v-card-title>
        <span class="font-weight-bold text-h5" v-if="content">{{ content }}</span>
        <span class="font-weight-bold text-h5" v-else>배송지 등록</span>
      </v-card-title>
      <v-card-text class="caption"><span class="main-txt" v-if="!title">배송지 등록은 선택사항입니다.</span></v-card-text>
      <v-card-text class="caption pb-0"><span class="main-txt">*</span> 표시는 필수 사항입니다.</v-card-text>
      <v-card-text>
        <v-container>
          <v-form ref="addform">
            <v-row no-gutters>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="배송지명 *" v-model="nowarrname" :rules="nameRule"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="수신자명 *" v-model="nowtoname" :rules="nameRule"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="휴대폰번호 *" v-model="nowtophone" :rules="phoneRule"></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field label="우편번호 *" v-model="nowarr1" :rules="arr1Rule" readonly></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field label="배송지 주소 *" v-model="nowarr2" :rules="arr2Rule" readonly></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field label="상세 주소" v-model="nowarr3"></v-text-field>
              </v-col>
              <v-col v-if="addDefault">
                <v-select :items="items" v-model="isDefault" label="기본 배송지 지정"></v-select>
              </v-col>
              <v-col cols="12">
                <v-btn class="primary" @click="openDaumPostcode">주소 검색</v-btn>
              </v-col>
            </v-row>
            <v-row>
              <div ref="wrap" style="display: none; border: 1px solid; width: 320px; height: 300px; margin: 5px 0; position: relative">
                <v-img
                  src="//t1.daumcdn.net/postcode/resource/images/close.png"
                  style="cursor: pointer; position: absolute; right: 0px; top: -1px; z-index: 1"
                  @click="foldDaumPostcode"
                  alt="접기 버튼"
                />
              </div>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-btn color="green darken-1" text @click="clear"> 초기화 </v-btn>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" text @click="close"> 취소 </v-btn>
        <v-btn color="blue darken-1" text @click="inform"> 확인 </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  name: "Address",
  components: {},
  data() {
    return {
      items: ["YES", "NO"],
      cnt: 0,
      flag: false,
      addrflag: false,
      nowarrname: "",
      nowarr1: "",
      nowarr2: "",
      nowarr3: "",
      nowtoname: "",
      nowtophone: "",
      isDefault: "",
      phoneRule: [
        (v) => {
          if (v) {
            return v.trim().length > 0 || "필수 입력 사항입니다.";
          } else {
            return v || "필수 입력 사항입니다.";
          }
        },
        (value) => {
          const pattern = /^(01)[0-9]{1}-[0-9]{3,4}-[0-9]{4}$/;
          return pattern.test(value) || "핸드폰 번호 형식이 아닙니다.";
        },
      ],
      nameRule: [
        (v) => {
          if (v) {
            return v.trim().length > 0 || "필수 입력 사항입니다.";
          } else {
            return v || "필수 입력 사항입니다.";
          }
        },
      ],
      arr1Rule: [
        (v) => {
          if (v) {
            return v.trim().length > 0 || "필수 입력 사항입니다.";
          } else {
            return v || "필수 입력 사항입니다.";
          }
        },
        (v) => {
          const pattern = /[0-9]*/;
          return pattern.test(v) || "숫자만 입력 가능합니다.";
        },
      ],
      arr2Rule: [
        (v) => {
          if (v) {
            return v.trim().length > 0 || "필수 입력 사항입니다.";
          } else {
            return v || "필수 입력 사항입니다.";
          }
        },
      ],
    };
  },
  methods: {
    close() {
      this.flag = false;
    },
    inform() {
      if (this.$refs.addform.validate()) {
        if (!this.isDefault) {
          this.isDefault = "NO";
        }
        this.$emit(
          "inform",
          this.nowarrname,
          this.nowarr1,
          this.nowarr2,
          this.nowarr3,
          this.nowtoname,
          this.nowtophone,
          this.isDefault.substring(0, 1),
          this.ano,
        );
        this.flag = false;
      }
    },
    clear() {
      this.nowarrname = "";
      this.nowarr1 = "";
      this.nowarr2 = "";
      this.nowarr3 = "";
      this.nowtoname = "";
      this.nowtophone = "";
    },
    openDaumPostcode() {
      const elementWrap = this.$refs.wrap;
      // 현재 scroll 위치를 저장해놓는다.
      const currentScroll = Math.max(document.body.scrollTop, document.documentElement.scrollTop);
      new window.daum.Postcode({
        oncomplete: (data) => {
          // 검색결과 항목을 클릭했을때 실행할 코드를 작성하는 부분.

          // 각 주소의 노출 규칙에 따라 주소를 조합한다.
          // 내려오는 변수가 값이 없는 경우엔 공백('')값을 가지므로, 이를 참고하여 분기 한다.
          let addr = ""; // 주소 변수

          // 사용자가 선택한 주소 타입에 따라 해당 주소 값을 가져온다.
          if (data.userSelectedType === "R") {
            // 사용자가 도로명 주소를 선택했을 경우
            addr = data.roadAddress;
          } else {
            // 사용자가 지번 주소를 선택했을 경우(J)
            addr = data.jibunAddress;
          }

          this.nowarr1 = data.zonecode;
          this.nowarr2 = addr;

          // iframe을 넣은 element를 안보이게 한다.
          // (autoClose:false 기능을 이용한다면, 아래 코드를 제거해야 화면에서 사라지지 않는다.)
          elementWrap.style.display = "none";

          // 우편번호 찾기 화면이 보이기 이전으로 scroll 위치를 되돌린다.
          document.body.scrollTop = currentScroll;
        },
        // 우편번호 찾기 화면 크기가 조정되었을때 실행할 코드를 작성하는 부분. iframe을 넣은 element의 높이값을 조정한다.
        onresize: function (size) {
          elementWrap.style.height = size.height + "px";
        },
        width: "100%",
        height: "100%",
      }).embed(elementWrap);
      elementWrap.style.display = "block";
    },
    foldDaumPostcode() {
      const elementWrap = this.$refs.wrap;
      elementWrap.style.display = "none";
    },
  },
  props: ["toname", "tophone", "title", "btncolor", "content", "setcnt", "addr1", "addr2", "addr3", "aname", "addDefault", "ano"],
  watch: {
    flag: function () {
      let limit = this.setcnt == null ? 0 : this.setcnt;

      if (this.cnt <= limit) {
        this.nowtoname = this.toname;
        this.nowtophone = this.tophone;
        this.nowarrname = this.aname;
        this.nowarr1 = this.addr1;
        this.nowarr2 = this.addr2;
        this.nowarr3 = this.addr3;
        this.isDefault = this.addDefault;
        this.cnt += 1;
      }
    },
  },
};
</script>

<style scoped></style>
