<template>
  <v-container>
    <v-row><v-col class="font-weight-bold text-h6 pb-0">회원가입</v-col></v-row>
    <v-row class="pa-3 mt-0">
      <v-col>
        <v-form ref="form" lazy-validation>
          <v-text-field label="아이디" v-model="member.id" :counter="20" :rules="idRules"></v-text-field>
          <v-text-field label="비밀번호" type="password" v-model="member.password" :rules="passwordRules"></v-text-field>
          <v-text-field label="비밀번호 확인" type="password" v-model="member.checkpassword" :rules="samePassowrd"></v-text-field>
          <v-text-field label="이름" v-model="member.name" :rules="nameRule"></v-text-field>
          <v-text-field label="생년월일" v-model="member.birth" type="date" :rules="birthRule"></v-text-field>
          <v-text-field label="전화번호" :rules="phoneRule" v-model="member.phone"></v-text-field>
          <v-text-field label="이메일" v-model="member.email" :rules="emailRule"></v-text-field>
          <div>
            <Address :toname="member.name" :tophone="member.phone" @inform="getAddr" />
          </div>
          <v-container class="mt-5">
            <v-row>
              <v-col align="left">
                <v-btn class="mr-4" @click="validJoin" color="primary" medium> 가입하기 </v-btn>
              </v-col>
              <v-col align="right">
                <v-btn @click="clear" class="red white--text"> 초기화 </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-form></v-col
      >
    </v-row>
    <div class="text-center ma-2">
      <v-snackbar v-model="snackbar">
        {{ resultInfo }}
        <template v-slot:action="{ attrs }">
          <v-btn color="pink" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
        </template>
      </v-snackbar>
    </div>
  </v-container>
</template>

<script>
import memberApi from "@/apis/member";
import Address from "@/components/tool/Address.vue";

export default {
  components: { Address },
  name: "JoinForm",
  data: () => ({
    snackbar: false,
    resultInfo: "",
    member: {
      id: "",
      password: "",
      checkpassword: "",
      name: "",
      birth: "",
      phone: "",
      email: "",
    },
    address: {
      aname: "",
      addr1: "",
      addr2: "",
      addr3: "",
      toname: "",
      tophone: "",
      isdefault: "Y",
    },
    idRules: [
      (value) => {
        return String(value).length > 1 || "필수 요소입니다.";
      },
      (value) => {
        return String(value).length > 5 || "너무 짧은 아이디입니다.";
      },
      (value) => {
        const pattern = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/;
        return !pattern.test(value) || "아이디는 영문과 숫자만 가능합니다.";
      },
      (value) => {
        const pattern = /^[a-zA-Z]/;
        return pattern.test(value) || "아이디는 영문으로 시작해야합니다.";
      },
      (value) => {
        return String(value).length <= 20 || "너무 긴 아이디입니다.";
      },
    ],
    passwordRules: [
      (value) => {
        return String(value).length > 0 || "필수 요소입니다.";
      },
      (value) => {
        return String(value).length > 7 || "너무 짧은 비밀번호입니다.";
      },
    ],
    phoneRule: [
      (value) => {
        const pattern = /^(01)[0-9]{1}-[0-9]{3,4}-[0-9]{4}$/;
        return pattern.test(value) || "핸드폰 번호 형식이 아닙니다.";
      },
    ],
    emailRule: [(value) => String(value).length > 0 || "필수 요소입니다.", (value) => /.+@.+\..+/.test(value) || "이메일 형식이 아닙니다."],
    birthRule: [(value) => String(value).length > 9 || "필수 요소입니다."],
    nameRule: [(value) => String(value).length > 0 || "필수 요소입니다."],
  }),
  methods: {
    clear() {
      for (let key in this.member) {
        this.member[key] = "";
      }
    },
    async validJoin() {
      if (this.$refs.form.validate()) {
        try {
          let result = await memberApi.join(this.member);
          if (result.data.result == "success") {
            // 회원가입 성공 해당 아이디의 유저 기준으로 배송지 추가
            let check = false;
            for (let key in this.address) {
              if (key != "arr3" && this.address[key] == "") {
                check = true;
                break;
              }
            }

            if (check) {
              console.log("미입력값 존재");
            } else {
              // 배송지 등록
              const promise2 = await memberApi.addAddress(this.address, this.member.id);
              console.log(promise2);
            }

            this.$router.replace("/home/main");
          } else if (result.data.result == "duplicated") {
            // 이미 존재하는 아아디
            this.resultInfo = "이미 존재하는 아이디입니다.";
            this.snackbar = true;
          } else {
            // 오류로 실패
            this.resultInfo = "오류가 발생하였습니다.";
            this.snackbar = true;
          }
        } catch (error) {
          console.log("error");
        }
      }
    },
    getAddr(nowarrname, nowarr1, nowarr2, nowarr3, nowtoname, nowtophone) {
      this.address.aname = nowarrname;
      this.address.addr1 = nowarr1;
      this.address.addr2 = nowarr2;
      this.address.addr3 = nowarr3;
      this.address.toname = nowtoname;
      this.address.tophone = nowtophone;
    },
  },
  computed: {
    samePassowrd() {
      const rules = [
        (value) => String(value).length > 0 || "필수 요소입니다.",
        () => this.member.password === this.member.checkpassword || "비밀번호가 일치하지 않습니다.",
      ];
      return rules;
    },
  },
};
</script>

<style></style>
